	module.exports = {"data":{"post":{"html":"<p>Frauke has the sometimes annoying characteristic of using her accelerator as a stress-relief device. If, for whatever reason, she become angry or frustrated while driving, she steps on the accelerator and drives faster. In certain situations this can be counter-productive, for example, when she's having trouble finding her destination. She can't find the place she's looking for, she gets mad, steps on the gas, and drives right on past.</p>\n<p>When this happens, I've learned through experience that it's best not to say anything, since in these situations Frauke is not very receptive to suggestions.</p>\n<p>I was rudely reminded this morning of the relationship between receptiveness and technology this morning at 4 am. Our security company called and spoke on the answering machine, which is integrated in our telephone. Frauke wanted to return the call, but in her just-awakened-state was unable to locate the number in the phone, and called for me to find it for her (as if my just-awakened-state would be any clearer). </p>\n<p>Our Siemens phone stores incoming numbers in various places, depending on whether the call was taken, missed, or a message was left. In this case, to get the number one has to first listen to the message. Since Frauke wasn't interested in the message, only the number, she was unable to find it in her 4 am state of mind.</p>\n<p>Now if the call had come in on her iPhone, she would have had a much easier time finding the number. The iPhone interface is better for poor reception.</p>","fields":{"title":"Poor Reception","date":"28 May 2008","url":"https://papascott.de/2008-05-28-poor-reception/"}}},"pathContext":{"slug":"/2008-05-28-poor-reception/","prev":{"fields":{"title":"An Honor Killing in Hamburg","date":"23 May 2008","slug":"/2008-05-23-an-honor-killing-in-hamburg/"}},"next":{"fields":{"title":"Uncle Tom in the 21st Century","date":"05 Jun 2008","slug":"/2008-06-05-uncle-tom-in-the-21st-century/"}}}}

